.card {
    color: #212529;
    margin-bottom: $spacer-8;
    @include lg {
        margin-bottom: $spacer-5;
    }
    @include md {
        margin-bottom: $spacer-4;
    }
    img {
        width: 100%;
        height: auto;
    }
    .card-body {
        padding: $spacer-3 $spacer-6;
        border: 1px solid #00000020;
        @include lg {
            padding: $spacer-2 $spacer-4;
        }
    }
    .card-title {
        font-size: $font-size-h5; //24px
        @include lg {
            font-size: $font-size-m; //16px
        }
    }
    .card-footer {
        color: #707070;
    }
}